import { isObjEmpty } from "@utils";
import classNames from "classnames";
import {
  ContentState,
  convertFromHTML,
  convertFromRaw,
  convertToRaw,
  EditorState,
} from "draft-js";
import draftToHtml from "draftjs-to-html";
import { useEffect, useState } from "react";
import { Editor } from "react-draft-wysiwyg";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import Select from "react-select";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "../../../../constants/validate";
import { convertFileToBase64, uploadImage } from "../../../../helper/common";
import { htmlToDraftUtil } from "../../../../utility/Utils";
import { update, getCategoryId, getCategory } from "../store/action";
import avatarBlank from "../../../../assets/images/avatars/bg-blank.png";
import "./style.scss";

const CloudTab = ({ selected, intl }) => {
  const [avatar, setAvatar] = useState(avatarBlank);

  const store = useSelector((state) => state.products);
  const { instractors } = useSelector((state) => state.products);
  const { register, errors, handleSubmit } = useForm();
  const history = useHistory();
  const [productData, setProductData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const [fileImage, setFileImage] = useState();
  const [status, setStatus] = useState();
  const [allow, setAllow] = useState();
  const [type, setType] = useState();
  const { data } = useSelector((state) => state.category);
  const [changcate, setChangcate] = useState();
  const options = {
    value: store?.categoryDetail?.id,
    label: store?.categoryDetail?.name,
  };

  const [changeSelect, setChangeSelect] = useState(changcate);
  const [InstractorValue, setInstractors] = useState();
  const [disable, setDisable] = useState(false);
  const CloudOptions = validateOptions.CloudOptions;
  const dispatch = useDispatch();
  const changeCate = (e) => {
    setChangeSelect(e);
  };

  useEffect(() => {
    if (changcate) {
      setChangeSelect(changcate);
    }
  }, [changcate]);

  useEffect(() => {
    dispatch(
      getCategory({
        filter: {
          status: 1,
        },
        skip: 0,
        limit: 20,
        order: [
          {
            key: "status",
            value: "asc",
          },
          {
            key: "id",
            value: "desc",
          },
        ],
      })
    );
  }, [dispatch]);

  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };
  useEffect(() => {
    if (productData) {
      setAvatar(productData.image);
    }
  }, [productData]);
  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        productData !== null &&
        selected.id !== productData.id)
    ) {
      setProductData(selected);
    }
  }, [selected, productData]);

  const [valueDescription, setValueDescription] = useState(() =>
    EditorState.createEmpty()
  );
  const [valueSortDescription, setValueSortDescription] = useState(() =>
    EditorState.createEmpty()
  );
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const uploadCallback = async (file) => {
    var linkImg = avatarBlank;
    const imageBase64 = await convertFileToBase64(file);
    if (imageBase64) {
      const newData = imageBase64.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        const data = {
          imageData: newData[1],
          imageFormat: file?.type?.split("/")[1] || "png",
        };
        await uploadImage(data, file).then((res) => {
          linkImg = res?.data;
        });
      }
    }
    return new Promise((resolve, reject) => {
      resolve({ data: { link: linkImg } });
    });
  };
  const onSubmit = async (values) => {
    const product_description = draftToHtml(
      convertToRaw(valueDescription.getCurrentContent())
    );

    const product_sort_description = draftToHtml(
      convertToRaw(valueSortDescription.getCurrentContent())
    );

    if (isObjEmpty(errors)) {
      setDisable(true);
      var urlImage = "";
      if (avatar) {
        const newData = avatar.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: fileImage.type.split("/")[1],
          };
          const res = await uploadImage(data, fileImage).then((res) => {
            urlImage = res?.data;
          });
        } else {
          urlImage = avatar;
        }
        if (urlImage) {
          dispatch(
            update({
              id: parseInt(productData.id),
              data: {
                product_name: values?.product_name || "",
                sort_product_description:
                  product_sort_description ||
                  productData?.product_descriptions[0]?.sort_description,
                url_ytb: values.url_ytb || " ",
                url_demo: values.url_demo || " ",
                url_tutorial: values.url_tutorial || " ",
                product_description:
                  product_description ||
                  productData?.product_descriptions[0]?.product_description,
                lang,
                product_type: 3,
                status: parseInt(values.status),
                image: urlImage,
                // categoryProductId: parseInt(changeSelect?.value) || undefined,
                categoryProductId: 58,
                instructorId: parseInt(InstractorValue?.value) || undefined,
                link_trial: "/",
                link_download: "/", //cancel
                open: 1,
                is_monitor: 0,
                vat: values.vat || 0,
                // allow_display: parseInt(allow),
                allow_display: 1,
                priority_index: parseInt(values?.priority_index),
              },
            })
          );
        }
      }
    }
  };

  useEffect(() => {
    if (productData) {
      setStatus(parseInt(productData.status));
      setAllow(parseInt(productData.allow_display));
      setType(parseInt(productData.product_type));
      setValueDescription(
        htmlToDraftUtil(
          productData?.product_descriptions[0]?.description || " "
        )
      );
      setValueSortDescription(
        htmlToDraftUtil(
          productData?.product_descriptions[0]?.sort_description || " "
        )
      );
    }
  }, [productData]);

  useEffect(() => {
    dispatch(getCategoryId(selected?.categoryProductId));
  }, [dispatch, selected?.categoryProductId]);

  useEffect(() => {
    if (store?.status == 200) {
      history.push("/apps/categoryCloud/list");
    }
  }, [store?.status, history]);
  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.username} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="priority_index">
              <FormattedMessage id="index" />{" "}
            </Label>
            <Input
              name="priority_index"
              id="priority_index"
              placeholder=" "
              innerRef={register(CloudOptions.last_name)}
              onBlur={() => {
                let linkD = document.getElementById("priority_index");
                if (linkD && linkD.value) {
                  linkD.value = linkD.value.trim();
                }
              }}
              className={classNames({ "is-invalid": errors["last_name"] })}
              defaultValue={productData?.priority_index}
            />
          </FormGroup>
          <FormGroup>
            <Label for="product_name">
              <FormattedMessage id="Cloud name" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="product_name"
              id="product_name"
              innerRef={register(CloudOptions.product_name)}
              onBlur={() => {
                let productName1 = document.getElementById("product_name");
                if (productName1 && productName1?.value) {
                  productName1.value = productName1?.value.trim();
                }
              }}
              className={classNames({ "is-invalid": errors["product_name"] })}
              defaultValue={productData?.product_names[0]?.name}
            />
            <small className="text-danger">
              {errors?.product_name && errors.product_name.message}
            </small>
            {errors?.product_name?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid product's name" />
              </small>
            )}
          </FormGroup>

          <FormGroup>
            <Label for="vat">
              <FormattedMessage id="VAT" />{" "}
            </Label>
            <Input
              name="vat"
              id="vat"
              type="number"
              innerRef={register(CloudOptions.vat)}
              onBlur={() => {
                let vat = document.getElementById("vat");
                if (vat && vat.value) {
                  vat.value = vat.value.trim();
                }
              }}
              className={classNames({ "is-invalid": errors["vat"] })}
              defaultValue={productData?.vat}
            />
            <small className="text-danger">
              {errors?.vat && errors.vat.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="url_ytb">
              <FormattedMessage id="Link Developement" />{" "}
            </Label>
            <Input
              name="url_ytb"
              id="url_ytb"
              placeholder=" "
              innerRef={register(CloudOptions.url_ytb)}
              className={classNames({ "is-invalid": errors["url_ytb"] })}
              defaultValue={productData?.url_ytb}
            />
          </FormGroup>

          <FormGroup>
            <Label for="url_demo">
              <FormattedMessage id="Link Demo" />{" "}
            </Label>
            <Input
              name="url_demo"
              id="url_demo"
              placeholder=" "
              innerRef={register(CloudOptions.url_demo)}
              className={classNames({ "is-invalid": errors["url_demo"] })}
              defaultValue={productData?.url_demo}
            />
          </FormGroup>

          <FormGroup>
            <Label for="url_tutorial">
              <FormattedMessage id="Link Tutorial" />{" "}
            </Label>
            <Input
              name="url_tutorial"
              id="url_tutorial"
              placeholder=" "
              innerRef={register(CloudOptions.url_tutorial)}
              className={classNames({ "is-invalid": errors["url_tutorial"] })}
              defaultValue={productData?.url_tutorial}
            />
          </FormGroup>

          <FormGroup>
            <Label for="sort_description">
              <FormattedMessage id="Short Content" />{" "}
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                options: ["inline", "textAlign"],
                inline: {
                  inDropdown: false,
                  options: ["bold", "italic", "underline"],
                },
              }}
              editorState={valueSortDescription}
              onEditorStateChange={(data) => setValueSortDescription(data)}
              name="sort_description"
              innerRef={register(CloudOptions.sort_description)}
              className={classNames({
                "is-invalid": errors["sort_description"],
              })}
            />
            <small className="text-danger">
              {errors?.sort_description && errors.sort_description.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="product_description">
              <FormattedMessage id="Description" />{" "}
              <span className="text-danger"></span>
            </Label>

            <Editor
              stripPastedStyles={true}
              toolbar={{
                // options: ["inline", "textAlign"],
                // inline: {
                //   inDropdown: false,
                //   options: ["bold", "italic", "underline"],
                // },
                image: { uploadCallback: uploadCallback },
              }}
              defaultEditorState={valueDescription}
              editorState={valueDescription}
              onEditorStateChange={(data) => setValueDescription(data)}
              name="product_description"
              innerRef={register(CloudOptions.description_en)}
              className={classNames({
                "is-invalid": errors["product_description"],
              })}
            />
            <small className="text-danger">
              {errors?.description_en && errors.description_en.message}
            </small>
          </FormGroup>

          <FormGroup>
            <Label for="status">
              <FormattedMessage id="status" />
            </Label>

            <Input
              type="select"
              name="status"
              value={status}
              onChange={(e) => setStatus(parseInt(e.target.value))}
              id="status"
              defaultValue={productData && productData.status}
              innerRef={register({ required: true })}
            >
              <option value="1">{intl.formatMessage({ id: "Active" })}</option>
              <option value="2">{intl.formatMessage({ id: "Blocked" })}</option>
              {/* <option value="0">
                {intl.formatMessage({ id: "Deactive" })}
              </option> */}
            </Input>
          </FormGroup>

          {/* <FormGroup>
            <Label for="allow_display">
              <FormattedMessage id="Allow" />
            </Label>

            <Input
              type="select"
              name="allow_display"
              id="allow_display"
              value={allow}
              defaultValue={productData && productData.allow_display}
              innerRef={register({ required: true })}
              onChange={(e) => setAllow(parseInt(e.target.value))}
            >
              <option value="1">{intl.formatMessage({ id: "Allow" })}</option>
              <option value="0">
                {intl.formatMessage({ id: "Do not allow" })}
              </option>
            </Input>
          </FormGroup> */}

          {/* <FormGroup>
            <Label for="product_type">
              <FormattedMessage id="product_type" />
            </Label>

            <Input
              type="select"
              name="product_type"
              id="product_type"
              value={type}
              defaultValue={productData && productData.product_type}
              innerRef={register({ required: true })}
              onChange={(e) => setType(parseInt(e.target.value))}
            >
              <option value="1">{intl.formatMessage({ id: "Book" })}</option>
              <option value="2">{intl.formatMessage({ id: "service" })}</option>
              <option value="0">{intl.formatMessage({ id: "Course" })}</option>
            </Input>
          </FormGroup> */}

          {/* <FormGroup>
            <Label>
              <FormattedMessage id="Category" />
              <span className="text-danger"> * </span>
            </Label>
            <Select
              isClearable={false}
              onChange={(e) => changeCate(e)}
              innerRef={register({ required: true })}
              name="categoryId"
              value={changeSelect}
              placeholder={<FormattedMessage id="Select..." />}
              options={data?.map((item, index) => {
                return {
                  value: item?.id,
                  label: item?.name,
                };
              })}
              className="react-select"
              classNamePrefix="select"
            />
            <small className="text-danger">
              {errors?.categoryId && errors.categoryId.message}
            </small>
          </FormGroup> */}

          {/* {selected?.product_type == 0 && (
            <FormGroup>
              <Label>
                <FormattedMessage id="instructors" />
              </Label>
              <Select
                isClearable={false}
                onChange={(e) => setInstractors(e)}
                innerRef={register({ required: true })}
                name="instructorId"
                value={InstractorValue}
                placeholder={<FormattedMessage id="Select..." />}
                options={instractors?.map((item, index) => {
                  return {
                    value: item?.id,
                    label: `${item?.last_name} ${item?.first_name}`,
                  };
                })}
                className="react-select"
                classNamePrefix="select"
              />
              <small className="text-danger">
                {errors?.categoryId && errors.categoryId.message}
              </small>
            </FormGroup>
          )} */}

          <FormGroup>
            <Label for="Image">
              <FormattedMessage id="Image" />
              <span className="text-danger"> * </span>
            </Label>
            <Row className="align-items-end p-1">
              <Media className="mr-25" left>
                <Media
                  object
                  className="rounded mr-50 objectFit-contain"
                  src={avatar}
                  height="100"
                  width="100"
                />
              </Media>{" "}
              <Row className="flex-column px-1">
                <Button.Ripple tag={Label} className="mt-1" color="primary">
                  <FormattedMessage id="Upload" />
                  <Input
                    type="file"
                    onChange={(e) => onChange(e)}
                    hidden
                    accept="image/*"
                  />
                </Button.Ripple>
              </Row>
            </Row>
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              onClick={() => history.goBack()}
              color="secondary"
              outline
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CloudTab);
